import React from 'react';
import Carousel from 'react-bootstrap/Carousel';
import SliderItem from './SliderItem';
import bgSlider1 from '../../../components/assest/img/bgSlider1.png'
import bgSlider2 from '../../../components/assest/img/bgSlider2.jpg'
import bgSlider3 from '../../../components/assest/img/bgSlider3.jpg'


function Slider() {

  const objectData = {
    item1: { to:"/cources/digital", title: "  ", titleBold: "", description: "                                                                                                                                     .", img: bgSlider1 },
    item2: { to:"/cources/ing", title: "     ", titleBold: "", description: "                                                                                                                                     .", img: bgSlider2 },
    item3: { to:"/cources/network", title: "       ", titleBold: "", description: "                                                                                                                                     .", img: bgSlider3 },
};


  return (
    <Carousel interval={300000}>
      {Object.keys(objectData).map((key) => {
        const item = objectData[key];
        return (
          <Carousel.Item key={key}>
            <SliderItem
              data={{
                to: item.to,
                title: item.title,
                titleBold: item.titleBold,
                description: item.description,
                img: item.img
              }}
            />
          </Carousel.Item>
        );
      })}
    </Carousel>
  );
}

export default Slider;